import axios from "axios";

const managementService = {

  getList(params) {
    let url = '/rest/v1/server-management/list';
    return axios.post(url, params);
  },

  // 增加服务器
  addServer(params) {
    let url = '/rest/v1/server-management/add-server';
    return axios.post(url, params);
  },

  // 获取表头过滤信息
  getFilterList() {
    let url = '/rest/v1/server-management/list-filter-item';
    return axios.get(url);
  },

  deleteServer(id) {
    let url = '/rest/v1/server-management/delete-server';
    return axios.post(url);
  },

  // 获取词条信息
  getItem(id) {
    let url = `/rest/v1/drop-down-box/${id}/item`;
    return axios.post(url);
  }
}

export { managementService }